import React, { Component } from 'react';
import ReactPaginate from 'react-paginate';
import { connect } from 'react-redux';
import * as actions from '../actions';

import Loader from './Loader';
import VideoCard from './VideoCard';
import Paging from './Paging';

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      vinfo: null,
      loading: true
    };
  }
  componentDidMount() {
    this.props.loadVideos();
  }
  componentWillReceiveProps(nextProps) {
    console.log('componentWillReceiveProps -> ', nextProps);
    this.setState((prevState, props) => {
      return { vinfo: nextProps.vinfo, loading: nextProps.loading };
    });
  }

  loadVideos() {
    let url =
      'https://www.googleapis.com/youtube/v3/search?key=AIzaSyBZ-f3rkoW-Phj2xjQMX6Ao3M6imngHK_E&channelId=UCbG8GRAHE2-7Iw4Sj_zrGjg&part=snippet,id&order=date&maxResults=6&type=video';
  }

  handlePageClick(data) {
    console.log(data);
  }

  renderVideoList() {
    if (this.state.vinfo && this.state.vinfo.length > 0) {
      console.log('Render video list -> ', this.state.vinfo);
      return this.state.vinfo.map((v, i) => {
        return <VideoCard key={i} info={v.info} vid={v.vid} />;
      });
    }
  }

  render() {
    return (
      <div className="row">
        <div className="center">
          <ul className="pagination">
            {this.renderVideoList()}
            <ReactPaginate
              previousLabel={<i className="material-icons">chevron_left</i>}
              nextLabel={<i className="material-icons">chevron_right</i>}
              breakLabel={<a href="">...</a>}
              breakClassName={'break-me'}
              pageCount={100}
              marginPagesDisplayed={2}
              pageRangeDisplayed={5}
              onPageChange={this.handlePageClick}
              containerClassName={'pagination'}
              subContainerClassName={'pages pagination'}
              activeClassName={'active'}
            />
          </ul>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  console.log('mapStateToProps -> ', state);
  return { vinfo: state.videos.vinfo, loading: state.videos.loading };
};

export default connect(mapStateToProps, actions)(Home);



